    body {
         margin:  0; 
         padding: 0; 
    }
    
    .mainbody {
         /* margin:  0; 
         padding: 0; */
         padding: 0 20px 0 20px; 
        margin: 0;
       max-width: 780px;
        border: none;
     }
    
    .topbody {
        margin: 0;
        padding: 0 20px ;        
    }

 /*  (moved to vertical below)
    .vertical .mainimage { 
    width: 100%;
    padding: 0 0 0 0; 
    float: none;
}
    
    .vertical .description {
    float: none;
    width: 100%;
}
    */
    
    nav {
 	  padding: 10px 20px 10px 20px;
   /* letter-spacing: 1px; */
        font-size: 8pt;
    }
    
 
    .post-nav {
        margin: 10px 0 0 0;
        padding: 6px 5px 2px 5px; 
        
        letter-spacing: 0px;  
        font-size: 8pt;
	    line-height: 14pt;
    }
    
    .thumbnailimage {
        max-height: 200px;
        overflow: hidden;
        margin-bottom: 5px;
    }
    
    .thumbnavleft {
        margin-right: 0;
        padding-right: 4%;
}
    .thumbnavright {
        margin-left: 0;
        padding-left: 4%;
}
    
    footer {
 	  padding: 10px 0;
          margin: 0;
     }
    
     
   h1 {
       font-size: 22pt;
       line-height: 26pt;
   }
   

    
    h2 {
        font-size: 16pt;
        line-height: 20pt;
        /* font-size: 16pt;
        line-height: 18pt;
        font-weight: 700; */
    }
    
    h3 {
        font-size: 11pt;
        line-height: 14pt;
    }
    
    h4 {
        font-size: 10pt;
        line-height: 12pt;
    }
    
    p {
        font-size: 12pt;
        line-height: 18pt;
    }
    
 /*
    .jp {
        line-height: 16pt;
        font-size: 9pt;
    }
    
    .title_jp { 
        font-size: 15pt;
        line-height: 20pt;
       }
       */

    p.subtitle {
        font-size: 8pt;
        line-height: 10pt;
       letter-spacing: 1px;
        padding: 0;
        margin: 0;
     }
    
    .midsize /* hide on tablets */ {
        display: none;
    }
    
  
}

/* iPhone vertical only */
@media only screen and (max-width : 812px) and (orientation: portrait) { 
 
    .mainbody {
        margin: 0;
        padding: 0;
       }
    
    .topbody {
        padding: 0 20px ;
    }
    
    .mainimageleft /* back navi */ {
        height: 75%;

    }
    
      .vertical .mainimage { 
    width: 100%;
    padding: 0 0 0 0; 
    float: none;
}
    
    .vertical .description {
    float: none;
    width: 100%;
}
    
    nav {
 	  padding: 5px 10px 10px 10px;
     
    }
 
    .post-nav {
        margin: 0;
       padding: 6px 10px;
    }
    .post-nav div {
        padding: 0 4px;
}
    
    footer {
        padding: 10px;
        margin: 0;
     }
    /* 2022 flexbox 
     
    .footer1,
    .footer2,
    .footer3 {
        display: block;
        text-align: left;
        vertical-align: top;
        margin: 20px 0 ;
        line-height: 16pt;
    }
    */
    
    footer img._thumb {
      //width: 50%;
      max-width: 200px;
      padding: 10px 0;
    }
    
    footer .navitem, footer br a {
      line-height: 18pt;
    }
    


    
    .midsize /* hide on mobile */ {
        display: none;
    }
    
    .mobileyes /* hide on mobile */ {
        display: none;
    }
    
    .mobile  /* show on mobile */ {
        display: block;
        font-size: 9pt;
        /*color: #888;*/
    }

    .mobileinline /* show on mobile display inline */ {
      display: inline;
    }

    .thumbnail {
        width: 47%; 
        margin: 0 1px 20px 1px;

        }
    
    .thumbnailgroup.float {   
        width: 47%;
    }
    

        /*　Subimages */

    .subimagegroup {
        text-align: center;
    }


    .subimage {
        display: inline-block;
        width: 90%;
        overflow: hidden;
        margin: 10px 2% 30px 2%;
        padding: 1px;
        text-align: center;
        font-size: 9pt;
        line-height: 12pt;
        font-weight: 300;
        vertical-align: middle;
    }

    
    .photogallery li {
        margin: 0 0 20px 0;
        width: 97%;
        display: block;
        vertical-align: top;
    }
    
        /* // front page divs // */
    #frontpageleft, #frontpageright {
        width: 100%;
        float: none;
    }
    
}
